
.zhezhao {
  position: fixed;
  height: 100%;
  width: 100%;
  background: #bbb;
  z-index: 1040;
  background: rgba(255,255,255,.3);
}
.left-side {
  position: fixed;
  height: 100%;
  left: 20%;
  right: 20%;
  background: #fff;
  z-index: 1050;
  opacity: 1;
}
#app img {
  max-width: 100%;
  height: auto;
  display: block;
}
@media only screen and (max-width: 992px) {
.left-side {
    left: 10%;
    right:10%;
}
}
@media only screen and (max-width: 768px) {
.left-side {
    left: 0%;
    right: 0%;
}
}
.slide-left-enter-active {
  -webkit-animation: slide-left-in .5s ease;
          animation: slide-left-in .5s ease
}
.slide-left-leave-active {
  -webkit-animation: slide-left-out .5s ease;
          animation: slide-left-out .5s ease
}
@-webkit-keyframes slide-left-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0)
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes slide-left-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0)
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes slide-left-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes slide-left-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.show-zhezhao-leave-active {
  -webkit-animation: show-zhezhao-out .4s ease;
          animation: show-zhezhao-out .4s ease
}
